@import url('https://fonts.googleapis.com/css2?family=Source+Code+Pro&display=swap');

.nav {
  height: 10vh;
  background-color: #333;
  color: white;
  display: flex;
  justify-content: space-between;
  align-items: stretch;
  gap: 2rem;
  padding: 1.5rem 1rem;
  font-family: 'Source Code Pro', monospace;
}

.title {
  font-size: 2rem;
}

.title:active {
}

.nav ul {
  padding: 0;
  margin: 0;
  list-style: none;
  display: flex;
  gap: 1rem;
  font-family: 'Source Code Pro', monospace;
}

.nav a {
  color: inherit;
  text-decoration: none;
  height: 100%;
  display: flex;
  align-items: center;
  padding: .25rem;
}

.nav li:hover {
  background-color: #333;
}

.nav li.active {
  overflow: hidden;
  border-right: .15em solid white;
  white-space: nowrap;
  animation: 
    blink .75s step-end infinite;
}

.nav ul li {
  font-family: 'Source Code Pro', monospace;
  overflow: hidden;
  border-right: .15em solid transparent;
  white-space: nowrap;
}

/*Animations*/

@keyframes blink {
  from, to { border-color: transparent }
  50% { border-color: white }
}